import React from "react"
import './FileUpload.css'

//MODALS must be rendered with portals. This is to avoid issues and make sure that they always render on top all
//We must add code to index.html & index.js. Watch video on full explination if in doubt
//We imoprt this to use method to create portal
import ReactDom from 'react-dom'

const MODAL_STYLES = {
    position: 'fixed',
    top: '50%',
    left:'50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: '#FFF',
    zIndex: 1000
}

const OVERLAY_STYLES = {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0,0,0,.7)',
    zIndex: 1000
}

const Model = ({open, children, onClose}) => {

    if(!open){return  null}

    return ReactDom.createPortal(
        <>
            <div style={OVERLAY_STYLES}></div>
            <div style={MODAL_STYLES}>
                <div className = 'modal-content-flex'>
                    <button className='close-modal' onClick={onClose}>X</button>
                    <div className='modal-content'>
                    {children}
                    </div>
                </div>
            </div>
        </>,
        document.getElementById('portal')
    )
}

export default Model


