import { useState } from 'react';
import './App.css';
//import FileUpload from './components/FileUpload';
import FileUploadTest from './components/FileUploadTest';
import Modal from './components/Modal'
//tech-challange-img - upload preset name claudinary
import Button from '@mui/material/Button';

const BUTTON_WRAPPER_STYLES = {
  position: 'relative',
  zIndex: 1
}

function App() {
  const [isOpen, setIsOpen] = useState(false)

  const modalHandler = (evetn) => {
    setIsOpen(true)
  }

  const modalClose = (event) => {
    setIsOpen(false)
  }

  return (
    <div className="App">
      <h1 className='title'>Make Them Vintage</h1>
      <div style={BUTTON_WRAPPER_STYLES}>
        <Button variant="outlined" className='button' onClick={modalHandler}>Caution</Button>
        <div>
          <Modal open={isOpen} onClose={modalClose}>This is a demo web application. Storage on cloudinary is limitted. Please keep in mind!</Modal>
        </div>
      </div>
      <FileUploadTest />
    </div>
  );
}


export default App;
